s físicas, químicas, y del uso para el que están destinados. Se agrupan en cuatro tipos: los metales, los polímeros, las cerámicas y los composites dentales.")]),_vm._m(4),_c('separador'),_vm._m(5),_c('p',{staticClass:"mb-5"},[_vm._v("Son materiales inorgánicos, no metálicos, no poliméricos, fabricados por compactación y sinterización a temperaturas elevadas. Se pueden dividir en óxidos metálicos y otros compuestos. Se introdujeron las cerámicas de óxido para implantes quirúrgicos, debido a su inercia a la biodegradación, alta fuerza, a características físicas como el color y la mínima conductividad térmica y eléctrica, y una amplia gama de propiedades elásticas específicas del material. En muchos casos; sin embargo, la baja ductilidad o fragilidad inherente ha dado lugar a limitaciones. La cerámica se ha utilizado a granel y más recientemente, como recubrimientos en metales y aleaciones. ")]),_vm._m(6),_c('p',[_vm._v("La proporción de los cristales de leucita en la fase cristalina puede variar desde el 22 al 50% en estas porcelanas, en tanto que la fase vítrea está formada de un vidrio de aluminio silicato. Las diferencias entre estos porcentajes del contenido de leucita pueden estar inducidas por diversos factores que incluyen: múltiples cocciones de la porcelana, tratamientos térmicos, enfriamientos, e inclusive, las largas exposiciones a la saliva hacen que la porcelana madure y contenga mayor porcentaje de leucita.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Trabajos de investigación demuestran que un porcentaje de leucita entre el 20 al 30% es capaz de mejorar las propiedades mecánicas de esta porcelana, mientras que a mayor porcentaje, entre el 30 y 50%, dichas propiedades no aumentan significativamente. ")]),_vm._m(7),_vm._m(8),_vm._m(9),_vm._m(10),_c('separador'),_vm._m(11),_vm._m(12),_vm._m(13),_vm._m(14),_vm._m(15),_vm._m(16),_vm._m(17),_vm._m(18),_c('separador'),_vm._m(19),_c('p',{staticClass:"mb-5"},[_vm._v("Durante la elaboración de un dispositivo en cerámica es importante identificar el material solicitado por el profesional de odontología, ya que de esto depende que el técnico pueda identificar los materiales y equipos con los que podrá desarrollar dicha actividad, partiendo de la información anterior durante la manipulación y mezcla de las cerámicas, por ejemplo, lo indicado es que se consiga una de consistencia espesa para ser aplicada generalmente con pincel. El exceso de líquido se elimina secando con un papel absorbente. El espesor del material puede variar dependiendo lógicamente de la anatomía dentaria.")]),_vm._m(20),_c('p',{staticClass:"mb-5"},[_vm._v("Durante la manipulación de los materiales dentales es importante apoyarse en la información que proporcionan las fichas técnicas que los proveedores facilitan, para identificar no solo los tiempos de trabajo o de manipulación sino también por la adhesión mecánica que es producida por el contacto de la porcelana con una superficie texturizada (con microrugosidades) hechas por partículas de óxido de aluminio lanzadas desde un arenador. Además, el arenado elimina irregularidades superficiales que concentran estrés. Es un mecanismo que guarda mucha similitud con el sistema adhesivo en superficies dentales grabadas con ácido.")]),_vm._m(21)],1)],1)}
